.catalog-page {
  .advantages {
    margin-top: 0; } }

.breadcrumbs {
  text-align: center;
  margin-bottom: 20px;
  &__link {
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.03em;
    font-size: 16px;
    font-family: $fontHeavy;
    margin-right: 20px;
    position: relative;
    color: transparentize($textColor, 0.3);
    &:after {
      content: '/';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -15px; }
    &:last-child {
      margin-right: 0;
      &:after {
        display: none; } }
    &.is-active {
      color: $textColor; } } }

.catalog-promo {
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: 100% 400px;
  background-color: $bgColor;
  background-image: url(../../img/catalog-promo.jpg);
  padding-top: 100px;
  &__title {
    margin-top: 0;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 55px;
    letter-spacing: 0.05em;
    font-size: 48px;
    font-family: $fontBold;
    color: $btnBgColor; }
  &__sorting {
    background-color: #fff;
    padding: 50px 70px 70px 70px;
    margin-bottom: 60px; }
  &__text {
    text-align: center;
    font-size: 20px;
    margin-top: 0;
    font-family: $fontBook;
    line-height: 150%;
    color: #707070;
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px; }
  &__mainsort {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-bottom: 60px; }
  &__input {
    visibility: hidden;
    display: none;
    &:checked + label svg {
      color: $btnBgColor; } }
  &__main-item {
    width: 16.66%;
    padding: 30px 15px 15px 15px;
    text-align: center;
    border-right: 2px solid #F5F5F5;
    transition: all $transitionSpeed $ease;
    &:hover {
      cursor: pointer;
      svg {
        color: $btnBgColor;
        transform: translateX(-50%) scale(1.05); } }
    &:last-child {
      border-right: 0; } }
  &__subtitle {
    position: relative;
    text-transform: uppercase;
    font-size: 14px;
    font-family: $fontBold;
    margin: 0;
    padding-top: 80px;
    color: $btnBgColor;
    &:first-child img {
      width: 66px;
      height: 62px; }
    svg {
      color: #B1BABE;
      font-size: 45px;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%); } }

  &__secondsort {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    input {
      display: none;
      &:checked + label {
        color: #fff;
        background-color: $btnBgColor; } } }
  &__second-item {
    margin-left: 7px;
    margin-right: 7px;
    background-color: #FFEDE9;
    color: $btnBgColor;
    text-transform: uppercase;
    text-align: center;
    font-size: 14px;
    font-family: $fontBold;
    padding: 12px 25px;
    transition: all $transitionSpeed $ease;
    &:hover {
      color: #fff;
      background-color: $btnBgColor;
      cursor: pointer; } } }

.sort-items {
  display: none;
  padding-bottom: 30px;
  background-color: $bgColor;
  &__form {
    display: flex;
    flex-direction: row;
    align-items: center; }
  &__all {
    margin-left: auto;
    font-size: 20px;
    font-family: $fontMedium;
    color: #707070; } }


.catalog-items {
  padding-bottom: 60px;
  background-color: $bgColor;
  background-image: linear-gradient(to bottom, $bgColor, #fff);
  .catalog-item {
    text-align: center; }
  .popular__wrap {
    margin-bottom: 30px;
    margin-left: -8px;
    margin-right: -8px; }
  .advantages__scroll-wrap {
    margin-bottom: 30px; }
  .advantages__item {
    border: 2px solid $bgColor;
    &:last-child {
      border: 0; } }
  &__btn {
    max-width: 240px;
    border-radius: 20px;
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background-color: $btnBgColor;
    color: #fff;
    padding: 16px 44px;
    font-size: 14px;
    letter-spacing: 0.05em;
    text-decoration: none;
    border-radius: 30px;
    text-transform: uppercase;
    font-family: $fontBold;
    transition: all $transitionSpeed $ease;
    &:hover {
      background-color: $btnBgHover; } } }

.catalog-info {
  background-color: #FBFBFB;
  &__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch; }
  &__descr {
    width: 50%;
    padding: 95px 0;
    padding-right: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  &__title {
    max-width: 550px;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 40px;
    font-size: 36px;
    letter-spacing: 0.05em;
    line-height: 130%;
    font-family: $fontBold;
    color: $btnBgColor; }
  &__text {
    max-width: 550px;
    margin: 0;
    font-size: 20px;
    line-height: 150%;
    font-family: $fontBook;
    color: #707070; }
  &__photo {
    width: 50%;
    img {
      display: block;
      width: 100%;
      height: 100%; } } }

@include r($hd) {
  .catalog-promo {
    &__sorting {
      padding-left: 40px;
      padding-right: 40px; } } }

@include r($wide) {
  .catalog-items {
    .catalog-item {
      width: calc(33.33% - 14px);
      &:nth-child(4) {
        display: none; } } } }

@include r($desktop) {
  .catalog-promo {
    padding-top: 60px;
    &__title {
      margin-bottom: 30px; }
    &__text {
      font-size: 16px; }
    &__sorting {
      padding-top: 27px;
      padding-bottom: 40px;
      margin-bottom: 25px; }
    &__subtitle {
      font-size: 10px;
      padding-top: 45px;
      svg {
        font-size: 30px; } }
    &__mainsort {
      margin-bottom: 20px; }
    &__main-item {
      padding: 10px; }
    &__second-item {
      font-size: 10px;
      padding: 12px 10px; } }

  .sort-items {
    &__form {
      flex-wrap: wrap;
      justify-content: center; }
    .selectric-wrapper {
      width: calc(33.33% - 15px);
      margin-left: 7px;
      margin-right: 7px; }
    &__all {
      margin-left: auto;
      margin-right: auto;
      margin-top: 30px; } }

  .catalog-items {
    .catalog-item {
      width: calc(50% - 14px);
      &:nth-child(3),
      &:nth-child(4) {
        display: none; } } }

  .catalog-info {
    &__wrap {
      flex-direction: column;
      align-items: flex-start; }
    &__descr {
      text-align: center;
      width: 100%;
      padding-right: 0;
      padding-top: 90px; }
    &__title {
      max-width: 100%;
      margin-bottom: 40px; }
    &__text {
      max-width: 100%;
      line-height: 150%; }
    &__photo {
      width: calc(100% + 60px);
      margin-left: -30px; } } }


@include r($tablet) {
  .catalog-promo {
    padding-top: 50px;
    &__title {
      font-size: 36px;
      margin-bottom: 50px; }
    &__sorting {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding-top: 30px;
      padding-left: 20px;
      padding-right: 20px; }
    &__text {
      font-size: 20px; }
    &__mainsort {
      flex-wrap: wrap;
      margin-bottom: 40px; }
    &__main-item {
      width: 50%;
      padding-top: 30px;
      padding-bottom: 30px;
      border: 2px solid #F5F5F5;
      border-bottom-color: transparent;
      border-right-color: transparent;
      &:nth-child(4),
      &:nth-child(8),
      &:nth-child(12) {
        border-right-color: #f5f5f5; }
      &:nth-last-child(3) {
        border-bottom-color: #f5f5f5; }
      &:nth-last-child(1) {
        border-right: 2px solid #f5f5f5;
        border-bottom-color: #f5f5f5; } }
    &__subtitle {
      font-size: 14px;
      padding-top: 70px;
      svg {
        font-size: 50px; } }
    &__secondsort {
      flex-wrap: wrap; }
    &__second-item {
      font-size: 14px;
      margin-bottom: 13px; } }

  .sort-items {
    .selectric-wrapper {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 14px; }
    &__all {
      display: none; } }

  .breadcrumbs {
    &__link {
      text-transform: none; } }

  .catalog-items {
    .popular__wrap {
      flex-wrap: wrap;
      margin-bottom: 0; }
    .catalog-item {
      width: 100%;
      margin-bottom: 30px;
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        display: none; } } }

  .catalog-info {
    &__descr {
      padding-top: 70px;
      padding-bottom: 60px; }
    &__title {
      font-size: 24px; } } }
