// MAIN SLIDER SECTION
.main-slider {
  .slick-dots,
  .owl-dots {
    bottom: 170px; }
  &__slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    padding-top: 186px;
    padding-bottom: 290px;
    text-align: center;
    &:hover {
      cursor: url(../../img/my-cursor.png), grab; }
    &--first {
      background-image: url(../../img/main-slide.jpg); }
    &--second {
      background-image: url(../../img/main-slide2.jpg); } }
  &__subtitle {
    text-transform: uppercase;
    font-size: 24px;
    position: relative;
    font-family: $fontHeavy;
    color: $textColor;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-image: url(../../img/wave.svg);
      width: 109px;
      height: 8px;
      background-size: 100%; }
    &:before {
      left: -70%; }
    &:after {
      right: -70%; } }
  &__title {
    text-transform: uppercase;
    text-align: center;
    font-size: 60px;
    letter-spacing: 0.05em;
    color: $btnBgColor;
    font-family: $fontBold;
    margin-top: 5px;
    margin-bottom: 22px; }
  &__descr {
    margin-top: 0;
    margin-bottom: 55px;
    font-size: 24px;
    color: $btnBgColor;
    font-family: $fontBold; }
  &__btn {
    display: inline-block;
    padding: 16px 44px;
    font-size: 14px;
    letter-spacing: 0.05em;
    text-decoration: none;
    background-color: $btnBgColor;
    color: #fff;
    border-radius: 30px;
    text-transform: uppercase;
    font-family: $fontDemi;
    transition: all $transitionSpeed $ease;
    &:hover {
      background-color: $btnBgHover;
      box-shadow: 0 0 0 2px $btnBgHover; } } }


