// PHOTOS SECTION
.photos {
  display: flex !important;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
  .owl-stage,
  .slick-list,
  .slick-track {
    display: flex !important;
    align-items: stretch !important; }
  &.owl-loaded {
    a {
      width: 100%;
      min-height: 100%; } }
  a {
    display: block;
    text-decoration: none;
    width: 14.3%;
    transition: all $transitionSpeed $ease;
    &:hover {
      opacity: 0.8;
      cursor: url(../../img/my-cursor.png), grab; } }
  &__item {
    display: block;
    height: 100%;
    min-height: 100%;
    width: 100%;
    max-width: 100%; } }

// NEWSLETTER SECTION
.newsletter {
  padding-top: 48px;
  padding-bottom: 52px;
  background-color: #F0F1F2;
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    background-repeat: no-repeat;
    background-image: url(../../img/mailing.svg);
    background-size: 100%;
    background-position: 0 100%;
    width: calc(50% - 50px);
    height: 144px;
    z-index: 1; }
  &__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    z-index: 2; }
  &__info {
    width: 40%; }
  &__form {
    width: 60%;
    text-align: right; }
  &__title {
    font-size: 24px;
    font-family: $fontBold;
    margin-top: 7px;
    text-transform: uppercase;
    color: $btnBgColor;
    margin-bottom: 8px; }
  &__subtitle {
    font-size: 18px;
    font-family: $fontHeavy;
    margin: 0;
    color: $btnBgColor; }
  &__form-wrap {
    max-width: 535px;
    margin-left: auto;
    position: relative;
    margin-bottom: 18px;
    &--error {
      .newsletter__error {
        display: block; }
      .newsletter__input {
        color: #F0827D; } } }
  &__input {
    text-align: left;
    border: 1px solid transparent;
    border-radius: 20px;
    display: block;
    width: 100%;
    background-color: #fff;
    padding: 10px 33px;
    font-size: 18px;
    font-family: $fontBook;
    color: $btnBgColor;
    transition: all $transitionSpeed $ease;
    &:focus {
      border-color: #DEDEDE; } }
  &__btn {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 14px;
    padding: 13px 42px;
    text-align: center;
    text-transform: uppercase;
    border: 0;
    background-color: $btnBgColor;
    color: #fff;
    font-family: $fontDemi;
    letter-spacing: 0.05em;
    border-radius: 20px;
    transition: all $transitionSpeed $ease;
    &:hover {
      background-color: $btnBgHover; } }
  &__error {
    display: none;
    text-align: left;
    font-size: 16px;
    font-family: $fontMedium;
    color: #F0827D;
    margin-top: 10px; }
  &__text {
    font-size: 16px;
    margin: 0;
    font-family: $fontBook;
    color: $textColor; }
  &__link {
    text-decoration: none;
    color: $btnBgColor;
    margin-left: 4px;
    border-bottom: 1px solid transparent;
    transition: all $transitionSpeed $ease;
    &:hover {
      border-color: $btnBgColor; } } }


