// buttons
button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  transition: all $transitionSpeed ease-in-out;
  &:focus, &:active {
    outline: none; }
  &--block {
    display: block;
    width: 100%; }

