.product-page {
  .recent-items {
    background-image: linear-gradient(to bottom, #F9F9F9, #fff);
    margin-top: 0;
    margin-bottom: 20px; } }
.zoomWindowContainer {
  div {
    z-index: 1 !important; } }

.product-section {
  padding-top: 100px;
  background-color: #F9F9F9;
  .breadcrumbs {
    margin-bottom: 0; }
  &__top-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px; }
  &__link {
    font-size: 16px;
    font-family: $fontHeavy;
    text-transform: uppercase;
    color: #9E9E9E;
    position: relative;
    letter-spacing: 0.03em;
    transition: all $transitionSpeed $ease;
    &:hover {
      color: $btnBgColor; }
    &--prev {
      padding-left: 20px;
      svg {
        position: absolute;
        transition: none;
        top: 50%;
        left: 0;
        transform: translateY(-50%); } }
    &--next {
      padding-right: 20px;
      svg {
        position: absolute;
        transition: none;
        top: 50%;
        right: 0;
        transform: translateY(-50%); } } }
  &__title {
    margin-top: 0;
    margin-bottom: 18px;
    text-transform: uppercase;
    color: $btnBgColor;
    text-align: center;
    font-size: 48px;
    font-family: $fontBold;
    letter-spacing: 0.05em; }
  &__articul {
    display: block;
    margin-bottom: 50px;
    text-align: center;
    font-size: 20px;
    font-family: $fontBook;
    color: #B6B6B6; }
  &__wrap {
    background-color: #fff;
    padding: 55px 55px 35px 55px;
    margin-bottom: 80px; }
  &__topbar {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 65px; }
  &__slider {
    width: 60%;
    position: relative; }
  &__slider-wrap {
    overflow: hidden;
    &:hover {
      cursor: url(../../img/search-black.svg), zoom-in; } }
  &__badge {
    position: absolute;
    z-index: 5;
    top: 45px;
    left: 0;
    font-size: 12px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-family: $fontBold;
    color: #fff;
    text-align: center;
    padding: 12px 30px 12px 25px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    max-width: 145px;
    &--hit {
      background-color: $hitColor; }
    &--action {
      background-color: $actionColor; }
    &--wait {
      background-color: $waitColor;
      text-align: left;
      border-top-right-radius: 25px;
      border-bottom-right-radius: 25px; }
    &--new {
      background-color: $newColor; }
    &--sale {
      background-color: $saleColor; } }
  &__share {
    position: absolute !important;
    left: 25px;
    bottom: 140px;
    z-index: 5;
    font-size: 26px;
    transition: all $transitionSpeed $ease;
    color: $btnBgColor;
    &.is-active {
      .product-section__socials {
        visibility: visible;
        opacity: 1;
        transform: translateX(0px); } }
    &:hover {
      cursor: pointer;
      svg {
        opacity: 0.8; } } }
  &__socials {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    margin-left: 30px;
    position: relative;
    top: 8px;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-20px);
    transition: all $transitionSpeed $ease; }
  &__social-link {
    margin-right: 7px;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 40px;
    height: 40px;
    transition: all $transitionSpeed $ease;
    &:hover {
      transform: scale(1.1); }
    &--fb {
      background-image: url(../../img/facebook.svg); }
    &--vk {
      background-image: url(../../img/vkontakte.svg); }
    &--tw {
      background-image: url(../../img/twitter.svg); }
    &--ok {
      background-image: url(../../img/ok.svg); } }
  &__slider-wrap {
    margin-bottom: 23px;
    position: relative;
    img {
      display: block;
      max-width: 100%; }
    .owl-nav {
      top: 50%;
      transform: translateY(-50%);
      z-index: 5; }
    .owl-prev,
    .owl-next,
    .slick-prev,
    .slick-next {
      width: 40px;
      height: 40px;
      border: 0;
      background-color: transparent;
      background-size: 20px 25px;
      &:hover {
        opacity: 0.8; } }
    .owl-prev {
      margin-left: 10px; }
    .owl-next {
      margin-right: 10px; } }
  &__photos {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
    margin-left: -6px;
    margin-right: -6px;
    &-item {
      margin-left: 6px;
      margin-right: 6px;
      width: calc(20% - 12px);
      overflow: hidden;
      border: 2px solid transparent;
      //box-shadow: inset 0 0 0 3px transparent
      transition: all $transitionSpeed $ease;
      &:hover,
      &.is-active {
        border-color: $btnBgColor; }

      &--video {
        border-color: #F1F1F1;
        text-align: center;
        //&:hover
 }        //box-shadow: inset 0 0 0 3px $btnBgColor
      img {
        display: block;
        width: 100%;
        height: 100%;
        height: auto;
        transition: all $transitionSpeed $ease; }
      span {
        display: block;
        text-align: center;
        position: relative;
        text-transform: uppercase;
        color: $btnBgColor;
        font-size: 14px;
        font-family: $fontBold;
        letter-spacing: 0.03em;
        padding-top: 50px; }
      svg {
        font-size: 22px;
        position: absolute;
        top: 20px;
        left: 50%;
        transform: translateX(-50%); } } }
  &__info {
    width: 40%;
    padding-left: 70px; }
  &__price-wrap {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    margin-bottom: 35px; }
  &__first-price,
  &__second-price {
    width: 45%;
    border: 2px solid #FFF4F2;
    padding: 12px 10px 12px 30px;
    font-size: 40px;
    font-family: $fontMedium;
    letter-spacing: -0.05em;
    color: $btnBgColor;
    svg {
      font-size: 24px; }
    span {
      display: block;
      font-size: 16px;
      font-family: $fontBook;
      color: transparentize($btnBgColor, 0.6);
      letter-spacing: 0; } }
  &__second-price {
    width: 55%;
    background-color: #FFF4F2; }
  &__amount-wrap {
    padding-bottom: 35px;
    position: relative;
    margin-bottom: 35px; }
  &__amount-title {
    font-size: 24px;
    font-family: $fontMedium;
    margin-top: 0;
    margin-bottom: 15px;
    color: $btnBgColor;
    font-weight: 400; }
  &__amount-subtitle {
    font-size: 18px;
    font-family: $fontBook;
    font-weight: 400;
    color: #B6B6B6;
    margin-top: 0;
    margin-bottom: 20px; }
  &__amount {
    display: flex;
    align-items: center;
    .cart__amount-wrap {
      margin-left: 0; } }
  &__amount-btns {
    display: flex;
    align-items: center;
    &--mobile {
      display: none; } }
  &__tocart {
    font-size: 14px;
    font-family: $fontBold;
    text-transform: uppercase;
    text-align: center;
    padding: 17px 55px 17px 35px;
    border-radius: 30px;
    color: $btnBgColor;
    background-color: $primary2Color;
    transition: all $transitionSpeed $ease;
    position: relative;
    svg {
      position: absolute;
      top: 50%;
      right: 30px;
      transform: translateY(-50%);
      font-size: 20px; }
    &--envelope {
      padding-left: 25px;
      padding-right: 55px;
      svg {
        font-size: 14px;
        right: 25px; } }
    &:hover {
      background-color: $primary2Hover;
      box-shadow: 0 0 0 2px $primary2Hover; } }
  &__like {
    border-radius: 50%;
    background-color: $primaryColor;
    color: $btnBgColor;
    text-align: center;
    font-size: 14px;
    width: 50px;
    flex: 0 0 auto;
    height: 50px;
    margin-left: 5px;
    transition: all $transitionSpeed $ease;
    svg {
      font-size: 16px;
      position: relative;
      top: 2px; }
    &:hover {
      background-color: $primaryHover;
      box-shadow: 0 0 0 2px $primaryHover; } }
  &__charac-wrap {
    &--mobile {
      display: none; } }
  &__charac-title {
    font-size: 24px;
    font-family: $fontMedium;
    margin-top: 0;
    margin-bottom: 20px;
    color: $btnBgColor;
    font-weight: 400; }
  &__charac-descr {
    font-size: 18px;
    font-family: $fontBook;
    color: #B6B6B6;
    margin-top: 0;
    margin-bottom: 10px;
    span {
      color: $btnBgColor; } }
  &__tabs {}
  &__links-wrap {
    display: flex;
    flex-direction: row;
    align-items: stretch; }
  &__tab {
    &-link {
      width: 25%;
      padding: 35px 60px 35px 150px;
      position: relative;
      border: 2px solid #F1F1F1;
      //box-shadow: inset 0 0 0 2px #f1f1f1
      border-left-color: transparent;
      transition: all $transitionSpeed $ease;
      font-size: 16px;
      text-transform: uppercase;
      letter-spacing: 0.03em;
      font-family: $fontDemi;
      z-index: 1;
      overflow: hidden;
      color: #B1BABE;
      svg {
        position: absolute;
        top: 50%;
        left: 80px;
        transform: translateY(-50%);
        transition: none; }
      .ico-descr {
        font-size: 40px; }
      .ico-card {
        font-size: 44px; }
      .ico-action {
        font-size: 52px;
        transform: translateY(-50%) rotate(-90deg);
        left: 90px; }
      .ico-truck {
        font-size: 38px;
        left: 65px; }
      &:first-child {
        border-left-color: #F1F1F1; }
      &:hover,
      &.is-active {
        border-color: $btnBgColor;
        //box-shadow: inset 0 0 0 2px $btnBgColor
        z-index: 2;
        color: $btnBgColor; } }
    &-item {
      display: none;
      padding-top: 50px;
      &.is-active {
        display: block; } }
    &-wrap {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between; }
    &-half {
      width: 48%; }
    &-title {
      font-size: 24px;
      font-family: $fontMedium;
      margin-top: 0;
      margin-bottom: 25px;
      font-weight: 400;
      color: $btnBgColor; }
    &-text {
      font-size: 20px;
      font-family: $fontBook;
      color: #707070;
      margin-top: 0;
      margin-bottom: 40px;
      line-height: 150%; }
    &-tags {
      display: flex;
      flex-direction: row;
      align-items: stretch;
      margin-bottom: 55px; }
    &-tag {
      font-size: 18px;
      font-family: $fontBook;
      text-align: center;
      padding: 10px 18px;
      color: #B1BABE;
      margin-right: 7px;
      background-color: #F9F9F9;
      transition: all $transitionSpeed $ease;
      &:hover {
        color: #fff;
        background-color: $btnBgColor; } } }
  &__care {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 55px;
    &-item {
      margin-right: 30px;
      transition: all $transitionSpeed $ease;
      color: $btnBgColor;
      font-size: 30px;
      position: relative;
      &:last-child {
        margin-right: 0; }
      &:hover {
        color: #B1BABE;
        cursor: pointer;
        .product-section__tooltip {
          transform: translate(-50%, 0px);
          visibility: visible;
          opacity: 1; } }
      svg {
        transition: none; }
      .ico-care2,
      .ico-care3,
      .ico-care6 {
        font-size: 40px; } } }
  &__tooltip {
    padding: 15px 5px;
    font-size: 16px;
    text-align: center;
    font-family: $fontBook;
    color: #7A7A7A;
    background-color: #F0F1F2;
    width: 220px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -60px;
    left: 50%;
    transition: all $transitionSpeed $ease;
    transform: translate(-50%, 10px);
    visibility: hidden;
    opacity: 0;
    &:before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      bottom: -7px;
      left: 50%;
      transform: translateX(-50%);
      border-style: solid;
      border-width: 10px 10px 0 10px;
      border-color: #F0F1F2 transparent transparent transparent; } } }

.you-like {
  margin-bottom: 95px;
  padding-top: 185px;
  position: relative;
  text-align: center;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 170px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url(../../img/wave-stamp.svg); }
  &__title {
    display: inline-block;
    vertical-align: middle;
    font-size: 28px;
    font-family: $fontBold;
    margin-top: 0;
    margin-bottom: 40px;
    padding: 15px;
    background-color: #FFEDE9;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    width: 530px;
    color: $btnBgColor; }
  .recent-items {
    &__item {
      width: 20%; } } }


@include r($hd) {
  .product-section {
    &__wrap {
      padding: 45px 45px 35px 45px; }
    &__info {
      padding-left: 60px; } } }

@include r($wide) {
  .product-section {
    &__link {
      display: none; }
    &__top-wrap {
      text-align: center;
      justify-content: center; }
    &__wrap {
      padding: 35px 40px 35px 40px; }
    &__info {
      padding-left: 35px; }
    &__first-price,
    &__second-price {
      font-size: 32px;
      padding: 12px 10px 12px 10px;
      svg {
        font-size: 20px; } }
    &__amount {
      flex-wrap: wrap;
      &-btns {
        margin-top: 15px;
        width: 100%; } }
    &__tocart {
      &--envelope {
        width: calc(100% - 55px);
        padding-left: 20px;
        padding-right: 40px;
        text-align: left;
        svg {
          right: 20px; } } }
    &__share {
      bottom: 100px; }
    &__photos {
      &-item {
        span {
          font-size: 0;
          padding-top: 0;
          height: 100%; }
        svg {
          font-size: 27px;
          top: 50%;
          transform: translate(-50%, -50%); } } }
    &__tab {
      &-link {
        padding: 35px 15px 35px 90px;
        svg {
          left: 30px; }
        .ico-action {
          left: 30px; }
        .ico-truck {
          left: 20px; } } } }



  .you-like {
    .recent-items__item {
      width: calc(25% - 16px);
      &:last-child {
        display: none !important; } } } }


@include r($desktop) {
  .product-section {
    padding-top: 55px;
    &__title {
      font-size: 36px; }
    &__articul {
      margin-bottom: 40px; }
    &__slider {
      width: 50%; }
    &__info {
      width: 50%;
      padding-left: 20px; }
    &__wrap {
      padding: 35px; }
    &__first-price,
    &__second-price {
      font-size: 30px;
      padding-right: 5px;
      svg {
        font-size: 18px; }
      span {
        font-size: 14px;
        letter-spacing: -0.03em; } }
    &__price-wrap {
      margin-bottom: 20px; }
    &__amount {
      &-title {
        font-size: 24px; }
      &-subtitle {
        font-size: 18px;
        font-family: $fontBook;
        margin-bottom: 12px; }
      .cart__amount-wrap {
        margin-right: auto; } }
    &__amount-btns {
      display: none;
      &--mobile {
        display: flex;
        justify-content: space-around;
        margin-bottom: 30px;
        position: relative;
        &:before {
          content: '';
          position: absolute;
          top: -50px;
          left: 0;
          width: 100%;
          height: 20px;
          background-size: 100% auto;
          background-repeat: no-repeat;
          background-image: url(../../img/product-wave.svg); } } }
    &__charac-wrap {
      display: none;
      &--mobile {
        display: block;
        margin-bottom: 50px; } }
    &__tocart,
    &__like {
      width: 40%;
      text-align: center;
      flex: 0 1 auto;
      border-radius: 30px;
      font-size: 16px;
      font-family: $fontBold;
      text-transform: uppercase;
      padding: 17px 85px 17px 55px;
      color: $btnBgColor;
      height: auto;
      margin-left: 0;
      span {
        position: relative; }
      svg {
        position: absolute;
        right: -45px;
        font-size: 18px; } }
    &__tocart--envelope {
      padding-left: 20px;
      padding-right: 20px;
      width: 56%;
      svg {
        display: none; } }
    &__badge {
      top: 20px;
      font-size: 10px; }
    &__slider-wrap {
      margin-bottom: 10px;
      .slick-prev,
      .slick-next {
        width: 30px;
        height: 30px;
        background-size: 15px 20px; }
      .slick-prev {
        left: 10px; }
      .slick-next {
        right: 10px; } }
    &__share {
      font-size: 22px;
      left: 15px;
      bottom: 80px; }
    &__photos-item {
      width: calc(20% - 6px);
      margin-left: 3px;
      margin-right: 3px;
      min-height: 55px;
      img {
        height: 100%; } }
    &__tab {
      &-link {
        padding: 25px 20px;
        text-align: center;
        padding-top: 90px;
        svg {
          top: 25px !important;
          left: 50% !important;
          transform: translate(-50%, 0); }
        .ico-action {
          left: 40% !important;
          transform: translateY(0) rotate(-90deg); } }

      &-item {
        padding-top: 30px; }
      &-wrap {
        flex-direction: column; }
      &-half {
        width: 100%; } } }

  .you-like {
    padding-top: 145px;
    margin-bottom: 80px;
    &:before {
      background-size: auto 100%; }
    .recent-items__item {
      width: calc(33.33% - 16px);
      &:nth-last-child(2) {
        display: none !important; } } } }

@include r($tablet) {
  .product-page {
    .recent-items {
      background-image: none;
      background-color: #fff; } }
  .product-section {
    &__articul {
      margin-bottom: 20px; }
    &__wrap {
      padding: 20px 30px;
      width: calc(100% + 60px);
      margin-left: -30px;
      margin-bottom: 0;
      padding-bottom: 120px; }
    &__topbar {
      flex-direction: column;
      margin-bottom: 30px; }
    &__slider {
      width: 100%; }
    &__info {
      width: 100%;
      padding-left: 0; }
    &__share {
      bottom: 130px; }
    &__photos {
      margin-bottom: 25px; }
    &__amount-wrap {
      padding: 0;
      margin: 0; }
    &__amount-btns--mobile {
      margin-bottom: 65px;
      justify-content: space-between;
      &:before {
        top: 95px; } }
    &__tocart {
      max-width: 250px;
      width: calc(100% - 65px);
      padding: 21px 70px 21px 60px;
      &--envelope {
        max-width: 450px;
        padding-left: 20px;
        padding-right: 20px; } }
    &__like {
      font-size: 0;
      width: 60px;
      height: 60px;
      padding: 0;
      span {
        display: block;
        height: 50px;
        width: 100%; }
      svg {
        font-size: 16px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); } }

    &__charac-wrap {
      text-align: center; }
    &__links-wrap {
      flex-direction: column; }
    &__tab-link {
      width: 100%;
      border-left-color: #F1F1F1;
      border-bottom-color: transparent;
      padding: 30px 50px 30px 130px;
      text-align: left;
      svg {
        left: 50px !important;
        top: 50% !important;
        transform: translateY(-50%); }
      .ico-action {
        left: 55px !important;
        transform: translateY(-50%) rotate(-90deg); }
      .ico-truck {
        left: 45px !important; }
      &:last-child {
        border-bottom-color: #F1F1F1; }
      &.is-active {
        border-bottom-color: $btnBgColor; } }
    &__tab-item {
      display: none !important; } }

  .you-like {
    padding-top: 100px;
    &:before {
      height: 100px; }
    &__title {
      font-size: 24px;
      width: 100%; }
    .recent-items {
      &__wrap {
        flex-wrap: wrap; }
      &__item {
        width: calc(50% - 16px);
        margin-bottom: 15px;
        &:nth-last-child(2) {
          display: flex !important; } } } } }



@include r($mobile) {
  .product-section {
    &__share {
      bottom: 100px; } } }
