@include r($desktop) {
  .cart {
    padding-top: 60px;
    &__wrapper {
      padding-top: 5px;
      padding-left: 50px;
      padding-right: 50px;
      padding-bottom: 40px; }
    &__title {
      font-size: 36px;
      margin-bottom: 30px; }
    &__header {
      display: none;
      &-amount,
      &-price,
      &-sum {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
        padding: 0;
        text-align: center;
        color: #B1BABE;
        font-size: 16px; }
      &-amount {
        margin-bottom: 12px; }
      &-price {
        text-align: left; }
      &-sum {
        margin-bottom: 2px; } }
    &__main {
      margin-bottom: 30px; }
    &__item {
      display: none;
      &--mobile {
        display: flex;
        align-items: stretch; } }
    &__middle {
      width: calc(50% - 77px); }
    &__last {
      width: calc(50% - 77px); }
    &__del {
      top: 10%;
      transform: none; }
    &__img {
      img {
        height: 100%;
        object-fit: cover; } }
    &__info,
    &__price,
    &__amount,
    &__sum {
      width: 100%; }
    &__price {
      text-align: left;
      margin-top: 25px; }
    &__sum {
      padding-left: 0;
      text-align: center;
      margin-top: 30px;
      font-size: 34px; }
    &__amount {
      margin-top: 30px; }
    &__promo {
      margin-top: 30px; }
    &__promo-wrap {
      max-width: 315px; }
    &__promo-input {
      width: 185px;
      padding-right: 20px;
      padding-left: 20px; }
    &__all {
      &-title {
        padding-bottom: 25px;
        margin-bottom: 20px; }
      &-btn {
        margin-top: 40px; } } }

  .recent-items {
    margin-top: 70px;
    &__title {
      font-size: 32px;
      span {
        width: 100%;
        display: block;
        &:before {
          display: none; }
        &:after {
          top: auto;

          left: 50%;
          transform: translateX(-50%);
          bottom: -30px; } } } } }


@include r($tablet) {
  .cart {
    &__wrapper {
      width: calc(100% + 60px);
      margin-left: -30px;
      padding: 5px 30px 20px 30px; }
    &__item--mobile {
      flex-wrap: wrap; }
    &__middle {
      width: calc(100% - 155px); }
    &__last {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center; }
    &__del {
      top: 25px;
      font-size: 18px; }
    &__info {
      padding-right: 0; }
    &__item-title {
      max-width: 100%; }
    &__sum {
      width: 125px;
      margin-right: 30px; }
    &__amount {
      width: calc(100% - 155px);
      .cart__header-amount {
        display: none; } }
    &__all {
      padding-left: 0;
      padding-right: 0; }
    &__promo-wrap {
      max-width: 100%;
      text-align: center;
      justify-content: center; }
    &__promo-text {
      text-align: center; }
    &__back {
      display: none;
      max-width: 300px;
      margin-left: auto;
      margin-right: auto;
      &--mobile {
        display: block;
        margin-top: 20px; } } }



  .recent-items {
    &__subtitle {
      margin-bottom: 20px;
      padding-left: 10px;
      padding-right: 10px; }
    &__price {
      padding-top: 20px; } } }
